export default {
	data() {
		return {
			goods:{}
		}
	},
	methods: {
		returnGoods(){
			var info=JSON.stringify(this.goods)
			uni.navigateTo({
				url:"/pages/applyReturn/applyReturn?info="+info
			})
		},
		exchange(){
			var info=JSON.stringify(this.goods)
			uni.navigateTo({
				url:"/pages/applyChange/applyChange?info="+info
			})
		}
	},
	onLoad(option) {
		//console.log(option)
	},
	mounted() {
		uni.getStorage({
			key:"regoods",
			success:(res)=>{
				console.log("regoods",res)
				this.goods=res.data;
			}
		})
	}
}
